import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import nav from '@/components/nav/index'
import login from '@/components/login/login'
import config from '@/components/config/index'
import stockLists from '@/components/stock/stocklists'
import moneyLists from '@/components/stock/moneylists'
import holiday from '@/components/stock/holiday'
import deliveryOrder from '@/components/stock/deliveryOrder'
import sellYingkui from '@/components/stock/sellYingkui'
import entrustLists from '@/components/stock/entrustLists'
import xrSerach from '@/components/stock/xrSerach'
import xrList from '@/components/stock/xrList'
import userExport from '@/components/stock/userExport'
import stockHold from '@/components/stock/stockHold'
import stockUser from '@/components/stock/stockUser'
import log from '@/components/log/index'
import systemUser from '@/components/system/user'
import systemAdmin from '@/components/system/admin'
import systemPic from '@/components/system/pic'

Vue.use(Router)

export default new Router({
  //base: '/public/',
  //mode: 'history',
  routes: [
    {
      path: '/',
      name: 'nav',
      component: nav,
      redirect: '/index',
      children:[
        //首页
        {
          path: '/index',
          name: 'index',
          component: index
        },
        {
          path: '/config',
          name: 'config',
          component: config
        },
        {
          path: '/systemUser',
          name: 'systemUser',
          component: systemUser
        },
        {
          path: '/xrList',
          name: 'xrList',
          component: xrList
        },
        {
          path: '/holiday',
          name: 'holiday',
          component: holiday
        },
        {
          path: '/entrustLists',
          name: 'entrustLists',
          component: entrustLists
        },
        {
          path: '/xrSerach',
          name: 'xrSerach',
          component: xrSerach
        },
        {
          path: '/userExport',
          name: 'userExport',
          component: userExport
        },
        {
          path: '/stockUser',
          name: 'stockUser',
          component: stockUser
        },
        {
          path: '/sellYingkui',
          name: 'sellYingkui',
          component: sellYingkui
        },
        {
          path: '/systemAdmin',
          name: 'systemAdmin',
          component: systemAdmin
        },
        {
          path: '/stockHold',
          name: 'stockHold',
          component: stockHold
        },
        {
          path: '/systemPic',
          name: 'systemPic',
          component: systemPic
        },
        {
          path: '/log',
          name: 'log',
          component: log
        },
        {
          path: '/deliveryOrder',
          name: 'deliveryOrder',
          component: deliveryOrder
        },
        {
          path: '/stockLists',
          name: 'stockLists',
          component: stockLists
        },
        {
          path: '/moneyLists',
          name: 'moneyLists',
          component: moneyLists
        },
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/nav',
      name: 'nav',
      component: nav
    },
  ]
})



