<template>
  <div>
    <el-card class="box-card">
      <div class="button-list">
        <el-row>
          <el-input placeholder="输入账号搜索" v-model.trim="user_name" size="small" style="width: 150px"></el-input>
          <el-input placeholder="输入股票代码搜索" v-model.trim="stock_code" size="small" style="width: 150px"></el-input>
          <!--<el-date-picker
                      v-model.trim="start"
                      type="date"
                      size="small"
                      value-format="yyyy-MM-dd"
                      placeholder="开始时间" width="170px" style="width: 160px">
                    </el-date-picker>
                    <el-date-picker
                      v-model.trim="end"
                      type="date"
                      size="small"
                      value-format="yyyy-MM-dd"
                      placeholder="结束时间" width="170px" style="width: 160px">
                    </el-date-picker>-->
          <el-button @click="getStock(1)" type="primary" size="small">查询</el-button>
          <el-button @click="PCstock(0)" type="primary" size="small">一键平仓</el-button>
          <el-button style="float: right" @click="getPg()" type="primary" size="small">配股</el-button>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <div class="button-list">
        <el-row>
          持仓盈亏总和：{{yk_total}}
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="Loading">
      <div class="text item">
        <el-table
          :data="stockData"
          border
          style="width: 100%"
          :height="tableHeight"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          @selection-change="handleSelectionChange"
          :header-cell-style="{'text-align':'center'}">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="user_name" label="用户账号"></el-table-column>
          <el-table-column prop="name" label="用户名"></el-table-column>
          <el-table-column sortable prop="stock_code" label="证券代码"></el-table-column>
          <el-table-column prop="stock_name" label="证券名称"></el-table-column>
          <el-table-column sortable prop="stock_num_total" label="股票总数量"></el-table-column>
          <el-table-column sortable prop="stock_num" label="可卖数量"></el-table-column>
          <el-table-column sortable prop="frozen_num" label="冻结数量"></el-table-column>
          <el-table-column sortable prop="yk" label="盈亏">
            <template slot-scope="scope">
              <span v-if="scope.row.yk <0" style="color: #070">{{scope.row.yk}}</span>
              <span v-if="scope.row.yk >0" style="color: #f00">{{scope.row.yk}}</span>
              <span v-if="scope.row.yk == 0">{{scope.row.yk}}</span>
            </template>
          </el-table-column>
          <el-table-column sortable prop="cbj" label="成本价">
            <template slot-scope="scope">
              {{parseFloat(scope.row.cbj)}}
            </template>
          </el-table-column>
          <el-table-column sortable prop="yk_bl" label="盈亏比例">
            <template slot-scope="scope">
              <span v-if="scope.row.yk_bl>0" style="color: #f00">{{scope.row.yk_bl}}%</span>
              <span v-if="scope.row.yk_bl<0" style="color: #070">{{scope.row.yk_bl}}%</span>
              <span v-if="scope.row.yk_bl==0">{{scope.row.yk_bl}}%</span>
              <!--<span :style="scope.row.yk_bl>0?'color:#f00;':'color:#070'">
                {{scope.row.yk_bl}}%
              </span>-->
            </template>
          </el-table-column>
          <el-table-column sortable prop="market_price" label="市价"></el-table-column>
          <el-table-column sortable prop="market_price_total" label="市值"></el-table-column>
          <el-table-column prop="market" label="交易市场"></el-table-column>
          <el-table-column label="编辑">
            <template slot-scope="scope">
              <el-button type="danger" size="mini" @click="PCstock(scope.row.id)">平仓</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="height: 15px"></div>
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="配股" :visible.sync="openstock" width="1200px">
      <div style="display: flex">
        <div style="flex: 1">
          <div class="password-inp">
            <div class="inp-title">用户账号</div>
            <el-input style="width: 150px" size="small" v-model.trim="addName" clearable></el-input>
          </div>
        </div>
        <div style="flex: 1">
          <div class="password-inp">
            <div class="inp-title">证券代码</div>
            <el-input style="width: 150px" size="small" v-model.trim="addCode" clearable @input="getCode"></el-input>
          </div>
        </div>
        <div style="flex: 1">
          <div class="password-inp">
            <div class="inp-title">证券名称</div>
            <el-input style="width: 150px" size="small" v-model.trim="addStockName" clearable></el-input>
          </div>
        </div>
        <div style="flex: 1">
          <div class="password-inp">
            <div class="inp-title">配股数量</div>
            <el-input style="width: 150px" size="small" v-model.trim="addNumber" @input="sumNumber()"
                      clearable></el-input>
          </div>
        </div>
        <div style="flex: 1">
          <div class="password-inp">
            <div class="inp-title">配股价格</div>
            <el-input style="width: 150px" size="small" v-model.trim="addPrice" @input="sumNumber()"
                      clearable></el-input>
          </div>
        </div>
        <div style="flex: 1">
          <div class="password-inp">
            <div class="inp-title">总金额</div>
            <el-input style="width: 150px" size="small" v-model.trim="allNumber" clearable></el-input>
          </div>
        </div>
        <div style="flex: 1">
          <div style="margin:28px 0 0 20px">
            <el-button size="small" @click="addPg()">执行</el-button>
          </div>
        </div>
      </div>
      <div style="margin-top: 20px">
        <el-table
          :data="pgList"
          border
          style="width: 100%"
          :height="600"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}">
          <el-table-column prop="update_time" label="日期" width="170"></el-table-column>
          <el-table-column prop="user_name" label="用户账号"></el-table-column>
          <el-table-column prop="name" label="用户名"></el-table-column>
          <el-table-column prop="stock_code" label="证券代码"></el-table-column>
          <el-table-column prop="stock_name" label="证券名称"></el-table-column>
          <el-table-column prop="stock_num" label="配股数量"></el-table-column>
          <el-table-column prop="stock_price" label="配股价格"></el-table-column>
          <el-table-column prop="stock_price_total" label="总价"></el-table-column>
        </el-table>
        <el-pagination @size-change="addSizeChange"
                       @current-change="addCurrentChange"
                       :current-page="addPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="addCount">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        id: '',
        Loading: false,
        openstock: false,
        user_name: '',
        total: 1,
        PageSize: 20,
        currentPage: 1,
        addPage: 1,
        stock_code: '',
        data: '',
        addName: '',
        addStockName: '',
        addCode: '',
        addNumber: 0,
        allNumber: 0,
        addPrice: 0,
        stockData: [{}],
        pgList: [{}],
        totalCount: 1,
        addCount: 1,
        tableHeight: 500,
        pageSizes: [20, 50, 150, 300, 1000],
        // 默认显示第几页
        start: '',
        yk_total: 0,
        end: '',
        timer: ''
      }
    },
    watch: {
      $route() {
        this.tableHeight = window.innerHeight - 360;
        this.getStock()
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight - 360;
    },
    mounted: function () {
      this.Loading = true;
      /*var now = new Date();
        var year = now.getFullYear(); //得到年份
        var month = now.getMonth();//得到月份
        var month = month+1;
        var date = now.getDate();
        this.start = year+'-'+month+'-'+date,
        this.end = year+'-'+month+'-'+date*/
      this.getStock()
    },
    beforeDestroy() {
      clearInterval(this.timer);
      this.timer = '';
    },
    methods: {
      getCode() {
        var _this = this
        if (this.addCode.length == 6) {
          this.$http.post('' + 'stock/admin/stockInfo', {
            'k': localStorage['stock_token'],
            'code': this.addCode
          })
            .then(function (res) {
              if (res.data.code != 200) {
                _this.$message({
                  message: res.data.msg,
                  type: 'warning'
                });
              } else {
                _this.addStockName = res.data.data.stock_name.value
              }
            })
        }
      },
      sumNumber() {
        var m = 0, s1 = this.addNumber.toString(), s2 = this.addPrice.toString();
        try {
          m += s1.split(".")[1].length
        } catch (e) {
        }
        try {
          m += s2.split(".")[1].length
        } catch (e) {
        }
        this.allNumber = (s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m);
      },
      addPg() {
        var _this = this
        this.$http.post('' + 'stock/admin/addPg', {
          'k': localStorage['stock_token'],
          'user_name': this.addName,
          'stock_code': this.addCode,
          'stock_number': this.addNumber,
          'stock_price': this.addPrice,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getPg()
              // _this.addName = '';
              // _this.addCode= '';
              // _this.addNumber= 0;
              // _this.addPrice=0
            }
          })
      },
      getPg() {
        var _this = this
        this.openstock = true;
        this.$http.post('' + 'stock/admin/pgList', {
          'k': localStorage['stock_token'],
          'page': this.addPage,
          'limit': this.PageSize,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.pgList = res.data.data.data
              _this.addCount = res.data.data.total
            }
          })
      },
      PCstock(id) {
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          if (id != 0) {
            this.id = id
          }
          this.stockPingCang()
        }).catch(() => {
        });
      },
      stockPingCang() {
        var _this = this
        this.$http.post('' + 'stock/admin/lcPcById', {
          'k': localStorage['stock_token'],
          'id': this.id,
        })
          .then(function (res) {
            _this.Loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.id = ''
              _this.getStock()
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      getStock(a) {
        if (a == 1) {
          this.currentPage = 1
        }
        var _this = this
        if(this.id != ''){
          clearTimeout(_this.timer)
          _this.timer = setTimeout(function () {
            _this.getStock();
          }, 1000)
          return
        }
        this.$http.post('' + 'stock/admin/lcList', {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
          'user_name': this.user_name,
          'stock_code': this.stock_code,
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            _this.Loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              clearTimeout(_this.timer)
              _this.timer = setTimeout(function () {
                  _this.getStock();
              }, 1000)
              //_this.stockData = res.data.data.map(Number);
              _this.yk_total = res.data.data.yk_total;
              _this.stockData = res.data.data.data.data;
              _this.totalCount = res.data.data.data.total
            }
          })
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        var s = ''
        for (var i = 0; i < val.length; i++) {
          s += val[i].id + ','
        }
        this.id = s.slice(0, -1);
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getStock(val, 1)
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getStock(this.PageSize, (val) * (this.pageSize))
      },
      addSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getPg(val, 1)
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.addPage = 1
      },
      // 显示第几页
      addCurrentChange(val) {
        // 改变默认的页数
        this.addPage = val
        // 切换页码时，要获取每页显示的条数
        this.getPg(this.PageSize, (val) * (this.pageSize))
      },
    }
  }
</script>

<style scoped>
  .inp-title {
    padding: 5px 0;
    width: 150px;
  }
</style>



