<template>
  <div>
    <el-card class="box-card" style="color: #F00;">
      注：以下查询结果只显示当日数据，历史数据请至交易端软件查询（ 查询快捷键 Ctrl+F10 ）
    </el-card>
    <div style="height: 20px;"></div>
    <el-card class="box-card">
      <div class="text item">
        <el-table
          :data="stockData"
          border
          :height="tableHeight"
          tooltip-effect="dark"
          style="width: 100%"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}">
          <el-table-column prop="M" label="股票市场"></el-table-column>
          <el-table-column prop="C" label="股票代码"></el-table-column>
          <el-table-column prop="N" label="简称"></el-table-column>
          <el-table-column prop="SF" label="是否分配"></el-table-column>
          <el-table-column prop="ND" label="年度"></el-table-column>
          <el-table-column prop="SG" label="送股"></el-table-column>
          <el-table-column prop="ZG" label="转增股"></el-table-column>
          <el-table-column prop="PX" label="派现"></el-table-column>
          <el-table-column prop="DJR" label="股权登记日"></el-table-column>
          <el-table-column prop="GBR" label="预案公布日"></el-table-column>
          <el-table-column prop="SSR" label="除权除息日"></el-table-column>
          <el-table-column prop="JD" label="进度"></el-table-column>
          <el-table-column prop="GGR" label="公告日"></el-table-column>
        </el-table>
      </div>
      <div style="height: 15px"></div>
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="pageSizes"
                     :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        total:0,
        PageSize:20,
        currentPage:1,
        tableHeight:500,
        stockData:[{}],
        pageSizes: [20],
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-260;
      this.getStock()
    },
    watch:{
      $route(){
        this.tableHeight = window.innerHeight-260;
        this.getStock()
      }
    },
    methods: {
      getStock() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize
        };
        this.data = this.KEY(str);
        this.$http.post(''+'stock/admin/cqcxInfo', {
          'data': this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.stockData = res.data.data.Obj;
              _this.total = res.data.data.Code
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getStock(val, 1)
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getStock(this.PageSize, (val) * (this.pageSize))
      },
    }
  }
</script>

<style scoped>
</style>



