<template>
  <div class="app">
    <el-card class="box-card">
      <div class="button-list">
        <el-row>
          <el-input placeholder="输入账户名" v-model.trim="code" size="small" style="width: 150px"></el-input>
          <el-button @click="usersAdd()" type="primary" size="small">搜索</el-button>
          <el-button @click="xrNow" size="small" type="danger" :loading="loading">今日一键除权除息</el-button>
          <el-button style="float: right" @click="xrShow = true" type="primary" size="small">手动除权除息</el-button>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        :data="tableData"
        style="width: 100%"
        stripe
        :height="tableHeight"
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="user_name" label="账户名"></el-table-column>
        <el-table-column prop="create_time" label="日期"></el-table-column>
        <el-table-column prop="stock_code" label="股票代码"></el-table-column>
        <el-table-column prop="stock_name" label="股票名称"></el-table-column>
        <el-table-column prop="stock_num" label="持仓数量	"></el-table-column>
        <el-table-column prop="send_stock_num" label="转送股"></el-table-column>
        <el-table-column prop="send_money" label="分红"></el-table-column>
        <el-table-column prop="remark" label="税率">
          <template slot-scope="scope">
            20%
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            {{scope.row.status == 2?'已撤销':'正常'}}
          </template>
        </el-table-column>
        <el-table-column prop="" label="操作">
          <template slot-scope="scope">
            <el-button type="danger" @click="cqcxCancel(scope.row.id)" size="mini">撤销</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="手动除权除息" :visible.sync="xrShow" width="300px">
      <div class="password-inp">
        <div style="padding: 5px 0">股票代码</div>
        <el-input placeholder="请输入股票代码" style="width: 200px" size="small" v-model.trim="xrcode" clearable></el-input>
      </div>
      <div class="password-inp">
        <div style="padding: 5px 0">送股</div>
        <el-input style="width: 200px" size="small" v-model.trim="sg" clearable></el-input>
      </div>
      <div class="password-inp">
        <div style="padding: 5px 0">增股</div>
        <el-input style="width: 200px" size="small" v-model.trim="zg" clearable></el-input>
      </div>
      <div class="password-inp">
        <div style="padding: 5px 0">派现</div>
        <el-input style="width: 200px" size="small" v-model.trim="px" clearable></el-input>
      </div>
      <div class="password-inp">
        <div style="padding: 5px 0">送股增股只用填写一个,若都填写按照增股算</div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="xrShow = false">取 消</el-button>
        <el-button type="primary" @click="xrAdd">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="除权除息" :visible.sync="TotalStock" width="1200px">
      <div class="">
        <el-table
          :data="TotalData"
          style="width: 100%"
          stripe
          :height="tableHeight"
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}"
          border>
          <el-table-column prop="user_name" label="账户名"></el-table-column>
          <el-table-column prop="create_time" label="日期"></el-table-column>
          <el-table-column prop="stock_code" label="股票代码"></el-table-column>
          <el-table-column prop="stock_name" label="股票名称"></el-table-column>
          <el-table-column prop="send_stock_num" label="转送股"></el-table-column>
          <el-table-column prop="send_money" label="分红"></el-table-column>
          <el-table-column prop="remark" label="税率">
            <template slot-scope="scope">
              20%
            </template>
          </el-table-column>
          <el-table-column prop="status" label="状态">
            <template slot-scope="scope">
              {{scope.row.status == 2?'已撤销':'正常'}}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="TotalStock = false">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        TotalData:[],
        TotalStock:false,
        loading:false,
        id:'',
        tableHeight:500,
        newUserShow:false,
        xrShow:false,
        total:1,
        sg:0,
        zg:0,
        px:0,
        PageSize:20,
        currentPage:1,
        code:'',
        xrcode:'',
        data:'',
        tableData:[{}],
        totalCount: 1,
        pageSizes: [20,50,150,300],
      }
    },
    watch:{
      $route(){
        this.tableHeight = window.innerHeight-280;
        this.getData()
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-280;
      this.getData()
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/cqcxList', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      xrNow(){
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.xrTotal()
        }).catch(() => {
        });
      },
      xrTotal(){
        this.loading = true
        var _this = this
        this.$http.post(''+'stock/admin/cqcxTotal', {
          'k': localStorage['stock_token'],
        })
          .then(function (res) {
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              if (res.data.data.length != 0){
                _this.TotalStock = true;
                _this.TotalData = res.data.data
              }
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      xrAdd(){
        var _this = this
        this.$http.post(''+'stock/admin/cqcxCustom', {
          'k': localStorage['stock_token'],
          'code':this.xrcode,
          'sg':this.sg,
          'zg':this.zg,
          'px':this.px,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      usersAdd() {
        var _this = this
        this.$http.post(''+'stock/admin/cqcxList', {
          'k': localStorage['stock_token'],
          'user_name':this.code,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
            }
          })
      },
      cqcxCancel(id){
        var _this = this
        this.$http.post(''+'stock/admin/cqcxCancel', {
          'k': localStorage['stock_token'],
          'id':id,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
    },
  }
</script>

<style scoped>
</style>



