<template>
  <div class="app">
    <el-card class="box-card">
      <div class="">
        <el-row :gutter="20">
          <el-input size="small" placeholder="输入用户账号搜索" v-model.trim="user_name" style="width: 150px"></el-input>
          <el-input size="small" placeholder="输入股票代码搜索" v-model.trim="stock_code" style="width: 150px"></el-input>
          <el-date-picker
            v-model.trim="start"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="开始时间" width="170px" style="width: 160px">
          </el-date-picker>
          <el-date-picker
            v-model.trim="end"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="结束时间" width="170px" style="width: 160px">
          </el-date-picker>
          <el-select v-model.trim="value" placeholder="全部" size="small" style="width: 160px ">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-button type="primary" @click="getData(1)" size="small">确认</el-button>
          <el-button v-loading="loading" style="float: right" type="primary" @click="exportData()" size="small">数据导出</el-button>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <div class="">
        <el-row :gutter="20">
          <div class="all-name">总手续费: {{name.sx_total}}</div>
          <div class="all-name">总印花税: {{name.yh_total}}</div>
          <div class="all-name">总过户费: {{name.ghf_total}}</div>
          <div class="all-name">合计: {{name.total}}</div>
          <div class="all-name">总盈亏: {{name.yingkui_total}}</div>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        id="table-data"
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="user_name" label="用户账号"></el-table-column>
        <el-table-column prop="name" label="用户名"></el-table-column>
        <el-table-column prop="update_time" label="成交时间" width="170px"></el-table-column>
        <el-table-column prop="stock_code" label="证券代码"></el-table-column>
        <el-table-column prop="stock_name" label="证券名称"></el-table-column>
        <el-table-column prop="date" label="动作">
          <template slot-scope="scope">
            <span v-if="scope.row.is_pg ==1">配股到账</span>
            <span v-if="scope.row.type==1 && scope.row.is_pg != 1">买入</span>
            <span v-if="scope.row.type!=1 && scope.row.is_pg != 1" style="color: #f00">卖出</span>
          </template>
        </el-table-column>
        <el-table-column prop="trade_num" label="成交数量"></el-table-column>
        <el-table-column prop="trade_price" label="成交价格"></el-table-column>
        <el-table-column label="成交金额">
          <template slot-scope="scope">
            {{parseFloat(scope.row.trade_price * scope.row.trade_num).toFixed(2)}}
          </template>
        </el-table-column>
        <el-table-column prop="sx" label="手续费"></el-table-column>
        <el-table-column prop="yk" label="印花税"></el-table-column>
        <el-table-column prop="ghf" label="过户费"></el-table-column>
        <el-table-column prop="yingkui" label="盈亏">
          <template slot-scope="scope">
            <span v-if="scope.row.yingkui<0" style="color: #070">{{scope.row.yingkui}}</span>
            <span v-if="scope.row.yingkui==0">{{scope.row.yingkui}}</span>
            <span v-if="scope.row.yingkui>0" style="color: #f00">{{scope.row.yingkui}}</span>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
  import fileSaver from 'file-saver'
  import xlsx from 'xlsx'
  export default {
    data() {
      return {
        loading:false,
        id: '',
        start: '',
        end: '',
        user_name: '',
        stock_code: '',
        name:[{}],
        // 总数据
        tableData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        tableHeight:500,
        // 个数选择器（可修改）
        pageSizes: [20,50,150,300],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
        value:'0',
        options: [{
          value: '0',
          label: '全部'
        }, {
          value: '1',
          label: '买入'
        }, {
          value: '2',
          label: '卖出'
        }],
      }
    },
    watch:{
      $route(){
        this.tableHeight = window.innerHeight-360;
        this.getData()
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-360;
    },
    mounted: function () {
      var now = new Date();
      var year = now.getFullYear(); //得到年份
      var month = now.getMonth();//得到月份
      var month = month+1;
      var date = now.getDate();
      this.start = year+'-'+month+'-'+date,
      this.end = year+'-'+month+'-'+date
      this.getData()
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData(a) {
        if(a == 1){
          this.currentPage = 1
        }
        var _this = this
        this.$http.post('' + 'stock/admin/jgList', {
          'k': localStorage['stock_token'],
          'trade_type':this.value,
          'page': this.currentPage,
          'limit': this.PageSize,
          'start': this.start,
          'end': this.end,
          'stock_code': this.stock_code,
          'user_name': this.user_name
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.name = res.data.data
              _this.tableData = res.data.data.list.data;
              _this.totalCount = res.data.data.list.total
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
      exportData(){
        this.loading = true
        var _this = this
        if(_this.totalCount > 5000){
          _this.$message({
            message: '导出数据最大数量为5000,请减少数据',
            type: 'warning'
          });
          _this.loading = false
          return
        }
        this.$http.post('' + 'stock/admin/jgList', {
          'k': localStorage['stock_token'],
          'trade_type':this.value,
          'page': 1,
          'limit': 5000,
          'start': this.start,
          'end': this.end,
          'stock_code': this.stock_code,
          'user_name': this.user_name
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.name = res.data.data
              _this.tableData = res.data.data.list.data;
              _this.totalCount = res.data.data.list.total
              if(_this.totalCount > 5000){
                _this.$message({
                  message: '导出数据最大数量为5000,请减少数据',
                  type: 'warning'
                });
                _this.loading = false
                return
              }
              setTimeout(function () {
                _this.exportTable()
              }, 3000)
            }
          })
      },
      exportTable() {
        /* 从表生成工作簿对象 */
        var wb = xlsx.utils.table_to_book(document.querySelector("#table-data"));
        /* 获取二进制字符串作为输出 */
        var wbout = xlsx.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        var now = new Date();
        var year = now.getFullYear(); //得到年份
        var month = now.getMonth();//得到月份
        month = month + 1;
        var date = now.getDate();
        var xlname = year + '' + month + '' + date
        var name = window.location.host + xlname
        try {
          fileSaver.saveAs(
            new Blob([wbout], {type: "application/octet-stream"}),
            //设置导出文件名称
            name + ".xlsx"
          );
          this.loading = false
        } catch (e) {
          if (typeof console !== "undefined") {
            alert('导出数据出错')
          }
        }
        return wbout;
      }
    },
  }
</script>

<style scoped>
  .all-name{
    font-size: 16px;
    width: 170px;
    float: left;
  }
</style>



