<template>
  <div>
    <el-card class="box-card">
      <div class="">
          <el-button @click="newShow = true" size="small">添加角色</el-button>
          <el-input style="width: 150px" placeholder="输入需要查询的角色名" size="small" v-model.trim="keyword"></el-input>
          <el-button type="primary" size="small" @click="searchUser()">搜索</el-button>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        :data="tableData"
        style="width: 100%" stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="title" label="角色名称"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            {{scope.row.status == 1?'开启':'关闭'}}
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="添加时间" width="170px"></el-table-column>
        <el-table-column prop="update_time" label="更新时间" width="170px"></el-table-column>
        <el-table-column prop="description" label="操作" width="255px">
          <template slot-scope="scope">
            <el-button style="margin: 0 5px" type="default" size="mini" @click="Permissions(scope.row.id,scope.row.rules)">权限分配</el-button>
            <el-button style="margin: 0 5px" type="primary"
                       @click="editShow = true ,id = scope.row.id,title= scope.row.title" size="mini">编辑
            </el-button>
            <el-button @click="deleteUser(scope.row.id)" style="margin: 0 5px" type="danger" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-dialog title="修改用户" :visible.sync="editShow" width="300px">
      <div class="password-inp">
        <el-input placeholder="请输入用户名" v-model.trim="title"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">状态</div>
        <el-radio v-model.trim="radio" label="1">开启</el-radio>
        <el-radio v-model.trim="radio" label="0">关闭</el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editShow = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="新增用户" :visible.sync="newShow" width="300px">
      <div class="password-inp">
        <el-input placeholder="请输入用户名" v-model.trim="title"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">状态</div>
        <el-radio v-model.trim="radio" label="1">开启</el-radio>
        <el-radio v-model.trim="radio" label="0">关闭</el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="newShow = false">取 消</el-button>
        <el-button type="primary" @click="newUser">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="权限分配" :visible.sync="PermissionsShow" width="500px">
      <div class="Permissions-title">交易管理</div>
        <el-checkbox-group v-model.trim="checkList">
          <el-checkbox label="/stockLists">股票管理</el-checkbox>
          <el-checkbox label="/holiday">节假日管理</el-checkbox>
          <el-checkbox label="/moneyLists">资金管理</el-checkbox>
          <el-checkbox label="/deliveryOrder">股票交割</el-checkbox>
          <el-checkbox label="/stockHold">股票持仓</el-checkbox>
          <el-checkbox label="/stockUser">账号管理</el-checkbox>
          <el-checkbox label="/entrustLists">委托列表</el-checkbox>
          <el-checkbox label="/sellYingkui">卖出盈亏</el-checkbox>
          <el-checkbox label="/xrList">除权除息</el-checkbox>
          <el-checkbox label="/xrSerach">除权除息查询</el-checkbox>
          <el-checkbox label="/userExport">数据导出</el-checkbox>
        </el-checkbox-group>
      <div class="Permissions-title">系统管理</div>
      <el-checkbox-group v-model.trim="checkList">
        <el-checkbox label="/systemUser">用户管理</el-checkbox>
        <el-checkbox label="/systemAdmin">角色管理</el-checkbox>
        <el-checkbox label="/config">配置管理</el-checkbox>
      </el-checkbox-group>
      <div class="Permissions-title">其他</div>
      <el-checkbox-group v-model.trim="checkList">
        <el-checkbox label="/systemPic">轮播图管理</el-checkbox>
        <el-checkbox label="/sendMessage">发送消息</el-checkbox>
      </el-checkbox-group>
      <div slot="footer" class="dialog-footer">
        <el-button @click="PermissionsShow = false">取 消</el-button>
        <el-button type="primary" @click="PermissionsSub">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        id: 0,
        checkList: [],
        radio: '1',
        title: '',
        keyword: '',
        editShow: false,
        newShow: false,
        PermissionsShow: false,
        // 总数据
        tableData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        // 个数选择器（可修改）
        pageSizes: [20,50,150,300],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
      }
    },
    mounted: function () {
      //console.log(window.location.pathname)
      var str = sessionStorage['menu']
      //console.log(str.indexOf(window.location.pathname) != -1 );
    },
    watch:{
      $route(){
        this.getData(this.PageSize, this.currentPage)
      }
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groups', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      Permissions(id,rules){
        this.PermissionsShow = true
        this.id = id
        this.checkList = rules.split(",")
      },
      PermissionsSub(){
        var _this = this
        var s = ''
        for (var i = 0; i<this.checkList.length; i++){
          s += this.checkList[i]+','
        }
        this.$http.post('' + 'stock/sys/editRule', {
          'k': localStorage['stock_token'],
          'rules': s.slice(0,-1),
          'id':this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      deleteUser(id) {
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteLog(id)
        }).catch(() => {
        });
      },
      editUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': this.id,
          'title': this.title,
          'status': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsEdit', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      newUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'title': this.title,
          'status': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsAdd', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      searchUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'keyword': this.keyword,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groups', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      deleteLog(id) {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': id
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsDelete', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
    },
    created: function () {
      this.getData(this.PageSize, this.currentPage)
    }
  }
</script>

<style scoped>
  .Permissions-title {
    font-size: 16px;
    padding-bottom: 20px;
  }
</style>



