<template>
  <div class="app">
    <el-card class="box-card">
      <div class="button-list">
        <el-row>
          <el-input placeholder="输入账户名搜索" v-model.trim="user_name" size="small" style="width: 150px"></el-input>
          <el-button @click="getData(1)" size="small">查询</el-button>
          <el-button @click="exportTable()" type="primary" size="small">导出数据</el-button>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="loading">
      <el-table
        id="table-data"
        :data="tableData"
        style="width: 100%"
        stripe
        :height="tableHeight"
        :summary-method="getSummaries"
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        show-summary
        border>
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="user_name" label="账户名"></el-table-column>
        <el-table-column prop="frozen_money" label="冻结资金"></el-table-column>
        <el-table-column sortable prop="market" label="持仓金额"></el-table-column>
        <el-table-column sortable prop="total_money" label="总资产"></el-table-column>
        <el-table-column sortable prop="user_money" label="可用金额"></el-table-column>
        <el-table-column sortable prop="initial_money" label="初始金额"></el-table-column>
        <el-table-column sortable prop="yk" label="浮动盈亏"></el-table-column>
        <el-table-column sortable prop="yk_bl" label="亏损比">
          <template slot-scope="scope">
            <span v-if="scope.row.yk_bl<0" style="color: #070">{{scope.row.yk_bl}}%</span>
            <span v-if="scope.row.yk_bl>0" style="color: #f00">{{scope.row.yk_bl}}%</span>
            <span v-if="scope.row.yk_bl==0">{{scope.row.yk_bl}}%</span>
          </template>
        </el-table-column>
        <el-table-column sortable prop="can_get_money" label="客户资金"></el-table-column>
        <el-table-column sortable prop="loan_money" label="系统垫资"></el-table-column>
        <el-table-column prop="end_time" width="110px" label="到期日期"></el-table-column>
        <el-table-column prop="remark" label="备注">
          <template slot-scope="scope">
            {{scope.row.state != 1 ?'':'已禁用'}}
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
    </el-card>
  </div>
</template>
<script>
  import fileSaver from 'file-saver'
  import xlsx from 'xlsx'

  export default {
    data() {
      return {
        loading: true,
        id: '',
        total: 1,
        PageSize: 20,
        currentPage: 1,
        data: '',
        state: '',
        tableHeight: 500,
        user_name: '',
        name: '',
        tableData: [{}],
        totalCount: 1,
        pageSizes: [20, 50, 150, 300],
      }
    },
    watch: {
      $route() {
        this.tableHeight = window.innerHeight - 245;
        this.getData()
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight - 245;
      this.getData()
    },
    mounted: function () {
      var now = new Date();
      var year = now.getFullYear(); //得到年份
      var month = now.getMonth();//得到月份
      month = month + 1;
      var date = now.getDate();
      var xlname = year + '' + month + '' + date
      this.name = window.location.host + xlname
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'all': 1,
          'user_name': this.user_name
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/users', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              var data = res.data.data.list.data
              for (var i = 0; i < data.length; i++) {
                data[i].total_money = parseFloat(data[i].total_money)
                data[i].user_money = parseFloat(data[i].user_money)
                data[i].initial_money = parseFloat(data[i].initial_money)
                data[i].yk = parseFloat(data[i].yk)
                data[i].yk_bl = parseFloat(data[i].yk_bl)
                data[i].market = parseFloat(data[i].market)
                data[i].can_get_money = parseFloat(data[i].can_get_money)
                data[i].loan_money = parseFloat(data[i].loan_money)
              }
              _this.tableData = data;
            }
          }).catch(error => {
          _this.$message({
            message: '请求超时,请刷新后重试',
            type: "error"
          });
        })
      },
      getSummaries(param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '合计';
            return;
          }
          if (index === 1) {
            sums[index] = '';
            return;
          }
          if (index === 8) {
            sums[index] = '';
            return;
          }
          if (index === 11) {
            sums[index] = '';
            return;
          }
          if (index === 12) {
            sums[index] = '';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return this.addSUM(value, prev)
          }, 0);
          sums[index];
        });
        return sums;
      },
      addSUM(arg1, arg2) {
        var r1, r2, m, c;
        try {
          r1 = arg1.toString().split(".")[1].length;
        }
        catch (e) {
          r1 = 0;
        }
        try {
          r2 = arg2.toString().split(".")[1].length;
        }
        catch (e) {
          r2 = 0;
        }
        c = Math.abs(r1 - r2);
        m = Math.pow(10, Math.max(r1, r2));
        if (c > 0) {
          var cm = Math.pow(10, c);
          if (r1 > r2) {
            arg1 = Number(arg1.toString().replace(".", ""));
            arg2 = Number(arg2.toString().replace(".", "")) * cm;
          } else {
            arg1 = Number(arg1.toString().replace(".", "")) * cm;
            arg2 = Number(arg2.toString().replace(".", ""));
          }
        } else {
          arg1 = Number(arg1.toString().replace(".", ""));
          arg2 = Number(arg2.toString().replace(".", ""));
        }
        return (arg1 + arg2) / m;
      },
      //定义导出Excel表格事件
      exportTable() {
        var _this = this
        /* 从表生成工作簿对象 */
        var wb = xlsx.utils.table_to_book(document.querySelector("#table-data"));
        /* 获取二进制字符串作为输出 */
        var wbout = xlsx.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        try {
          fileSaver.saveAs(
            new Blob([wbout], {type: "application/octet-stream"}),
            //设置导出文件名称
            _this.name + ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") {
            alert('导出数据出错')
          }
        }
        return wbout;
      }
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
  }
</script>

<style scoped>
</style>



