<template>
  <div class="app">
    <el-card class="box-card">
      <div class="">
            <el-button @click="addShow = true,title='添加股票',id=''" size="small">添加股票</el-button>
            <el-input style="width: 150px" placeholder="输入股票代码" v-model.trim="keyword" size="small"></el-input>
            <el-button type="primary" @click="getData(1)" size="small">搜索</el-button>
            <el-button type="warning" @click="stockUpdate(0)" size="small">开启/关闭</el-button>
            <el-button type="danger" @click="stockDelete(0)" size="small">删除</el-button>
            <el-button type="danger" @click="openStockShow = true" size="small">批量操作</el-button>
            <el-button type="danger" @click="updateName()" size="small" :loading="loading">名称更新</el-button>
            <el-button type="primary" @click="GetStock()" size="small" :loading="loading">更新新股</el-button>
      </div>
      <div class="title-red">
        <div>新股查询地址：<a href="http://data.eastmoney.com/xg/xg/default.html" target="view_window">http://data.eastmoney.com/xg/xg/default.html</a></div>
        <div>提示：每日开盘之前查询上市日期一列是否有上市新股，点击添加股票填写股票名称、代码、全称、简拼及开放交易日期。</div>
        <div>代码全称一栏请加上相应的股市交易市场简写，例如300001请填写SZ300001  600001填写SH600001</div>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="loading">
      <el-table
        :data="tableData"
        :height="tableHeight"
        style="width: 100%"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="stock_name" label="名称"></el-table-column>
        <el-table-column prop="code" label="代码"></el-table-column>
        <el-table-column prop="code_all" label="代码全称"></el-table-column>
        <el-table-column prop="jp" label="简称"></el-table-column>
        <el-table-column prop="open_time" label="开放交易日期"></el-table-column>
        <el-table-column sortable prop="is_open" label="允许交易">
          <template slot-scope="scope">
            <span :style="scope.row.is_open == 1?'color:#070':'color:#f00'">
               {{scope.row.is_open == 1?'开启':'关闭'}}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="description" label="编辑" width="240px">
          <template slot-scope="scope">
            <el-button @click="editStock(scope.row)" size="mini">编辑</el-button>
            <el-button v-if="scope.row.is_open == 1" type="warning" @click="stockUpdate(scope.row.id)" size="mini">关闭
            </el-button>
            <el-button v-if="scope.row.is_open != 1" type="success" @click="stockUpdate(scope.row.id)" size="mini">开启
            </el-button>
            <el-button type="danger" @click="stockDelete(scope.row.id)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog :title="title" :visible.sync="addShow" width="400px">
      <div class="password-inp">
        <div class="password-name">股票名称</div>
        <el-input placeholder="请输入股票名称" v-model.trim="name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">股票代码</div>
        <el-input placeholder="请输入股票代码" v-model.trim="code"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">代码全称</div>
        <el-input placeholder="请输入代码全称" v-model.trim="code_all"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">股票简拼</div>
        <el-input placeholder="请输入股票简拼" v-model.trim="jp"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">开启时间</div>
        <el-date-picker
          width="100%"
          v-model.trim="open_time"
          type="date"
          :default-date="open_time"
          value-format="yyyy-MM-dd"
          placeholder="选择日期">
        </el-date-picker>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">状态</div>
        <el-radio v-model.trim="radio" label="1">开启</el-radio>
        <el-radio v-model.trim="radio" label="0">关闭</el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addShow = false">取 消</el-button>
        <el-button type="primary" @click="addStock">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="批量操作" :visible.sync="openStockShow" width="400px">
      <div class="">
        <div class="password-name">输入股票代码,用英文逗号","隔开</div>
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          placeholder="请输入内容"
          v-model.trim="textarea">
        </el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="openStockShow = false">取 消</el-button>
        <el-button type="success" @click="openStock(1)">启用</el-button>
        <el-button type="danger" @click="openStock(0)">禁用</el-button>
      </div>
    </el-dialog>
    <el-dialog title="新增股票" :visible.sync="newStock" width="800px">
      <div class="">
        <el-table
          :data="newData"
          style="width: 100%"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}"
          border
        >
          <el-table-column prop="stock_name" label="名称"></el-table-column>
          <el-table-column prop="code" label="代码"></el-table-column>
          <el-table-column prop="code_all" label="代码全称"></el-table-column>
          <el-table-column prop="jp" label="简称"></el-table-column>
          <el-table-column prop="open_time" label="开放交易日期"></el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="newStock = false">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        newStock: false,
        tableHeight: 500,
        loading:false,
        textarea:'',
        openStockShow:false,
        title: '添加股票',
        id: '',
        keyword: '',
        addShow: false,
        name: '',
        code: '',
        code_all: '',
        jp: '',
        radio: '1',
        newData:[],
        open_time: '',
        // 总数据
        tableData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        // 个数选择器（可修改）
        pageSizes: [20,50,150,300,1000],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
      }
    },
    watch:{
      $route(){
        this.tableHeight = window.innerHeight-345;
        this.getData(this.PageSize, this.currentPage)
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-345;
      this.getData(this.PageSize, this.currentPage)
    },
    methods: {
      GetStock() {
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/newStockList', {
          data: this.data
        })
          .then(function (res) {
            _this.loading = false;
            _this.newData = res.data.data
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.newStock = true;
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      updateName(){
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/stockCheckSina', {
          data: this.data
        })
          .then(function (res) {
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData(a) {
        if(a == 1){
          this.currentPage = 1
        }
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
          'keyword': this.keyword
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/stockList', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      openStock(a) {
        var _this = this
        this.$http.post('' + 'stock/admin/mulitStockOpen', {
          'k': localStorage['stock_token'],
          'code': this.textarea,
          'open': a,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      stockUpdate(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/stockUpdate', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      stockDelete(id) {
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteStock(id)
        }).catch(() => {
        });
      },
      deleteStock(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/stockDelete', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      editStock(data) {
        this.title = '编辑股票'
        this.addShow = true
        this.id = data.id
        this.name = data.stock_name
        this.code = data.code
        this.code_all = data.code_all
        this.is_open = data.is_open
        this.jp = data.jp
        this.open_time = data.open_time
      },
      addStock() {
        if (this.id == ''){
          var url = 'stockAdd'
        } else {
          var url = 'stockEdit'
        }
        var _this = this
        this.$http.post('' + 'stock/admin/'+url, {
          'k': localStorage['stock_token'],
          'id': this.id,
          'stock_name': this.name,
          'code': this.code,
          'code_all': this.code_all,
          'is_open': this.radio,
          'jp': this.jp,
          'open_time': this.open_time,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.addShow = false
              _this.getData()
            }
          })
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        var s = ''
        for (var i = 0; i < val.length; i++) {
          s += val[i].id + ','
        }
        this.id = s.slice(0, -1);
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
    },
  }
</script>

<style scoped>
  .title-red{
    color: #f00;
    font-size: 15px;
    font-weight: bold;
    padding-top: 10px;
  }
  .password-inp {
    margin: 10px auto;
    max-width: 220px;
  }

  .password-name {
    font-size: 15px;
    padding: 5px;
  }
</style>



