<template>
  <div class="app">
    <el-card class="box-card">
      <div class="">
          <el-date-picker
            v-model.trim="keyword"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="选择日期" style="width: 160px">
          </el-date-picker>
          <el-button type="primary" @click="addHoliday()" size="small">添加</el-button>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        :data="tableData"
        :height="tableHeight"
        style="width: 100%" stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="date" label="选择日期"></el-table-column>
        <el-table-column prop="update_time" label="添加时间" width="170px"></el-table-column>
        <el-table-column prop="description" label="操作">
          <template slot-scope="scope">
            <el-button type="danger" @click="stockDelete(scope.row.id)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        loading:false,
        id: '',
        tableHeight:500,
        keyword: '',
        // 总数据
        tableData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        // 个数选择器（可修改）
        pageSizes: [20,50,150,300],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
      }
    },
    watch:{
      $route(){
        this.tableHeight = window.innerHeight-275;
        this.getData(this.PageSize, this.currentPage)
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-275;
      this.getData(this.PageSize, this.currentPage)
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
          'keyword': this.keyword
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/holidayList', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      stockDelete(id) {
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteStock(id)
        }).catch(() => {
        });
      },
      deleteStock(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/holidayDelete', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      addHoliday() {
        this.loading = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'date': this.keyword,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/holidayAdd', {
          'data':this.data
        })
          .then(function (res) {
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
    },
  }
</script>

<style scoped>
  .password-inp {
    margin: 10px auto;
    max-width: 220px;
  }

  .password-name {
    font-size: 15px;
    padding: 5px;
  }
</style>



