<template>
  <div>
    <el-card class="box-card">
      <div class="button-list">
        <el-row>
          <el-input placeholder="输入账户名搜索" v-model.trim="user_name" size="small" style="width: 150px"></el-input>
          <el-input placeholder="输入股票代码搜索" v-model.trim="stock_code" size="small" style="width: 150px"></el-input>
          <el-date-picker
            v-model.trim="start"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="开始时间" width="170px" style="width: 160px">
          </el-date-picker>
          <el-date-picker
            v-model.trim="end"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="结束时间" width="170px" style="width: 160px">
          </el-date-picker>
          <el-button @click="getStock(1)" type="primary" size="small">查询</el-button>
          <div class="tong">今日统计:{{todayAll}}</div>
          <div class="tong">本月统计:{{monthAll}}</div>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <div class="text item">
        <el-table
          :data="stockData"
          border
          tooltip-effect="dark"
          style="width: 100%"
          :height="tableHeight"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}">
          <el-table-column prop="user_name" label="用户账号"></el-table-column>
          <el-table-column prop="name" label="用户名"></el-table-column>
          <el-table-column sortable prop="stock_code" label="证券代码"></el-table-column>
          <el-table-column prop="stock_name" label="证券名称"></el-table-column>
          <el-table-column sortable prop="update_time" label="创建时间" width="170px"></el-table-column>
          <el-table-column sortable prop="update_time" label="交易时间" width="170px"></el-table-column>
          <el-table-column sortable prop="trade_total_money" label="交易金额"></el-table-column>
          <el-table-column sortable prop="yingkui" label="盈亏金额">
            <template slot-scope="scope">
              <span v-if="scope.row.yingkui <0" style="color: #070">{{scope.row.yingkui}}</span>
              <span v-if="scope.row.yingkui >0" style="color: #f00">{{scope.row.yingkui}}</span>
              <span v-if="scope.row.yingkui == 0">{{scope.row.yingkui}}</span>
            </template>
          </el-table-column>
        </el-table>
        <div style="height: 15px"></div>
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        todayAll:0,
        monthAll:0,
        user_name:'',
        total:1,
        PageSize:20,
        currentPage:1,
        data:'',
        stock_code:'',
        stockData:[{}],
        tableHeight:500,
        pageSizes: [20,50,150,300],
        start:'',
        end:'',
      }
    },
    watch:{
      $route(){
        this.getStock()
        this.tableHeight = window.innerHeight-280;
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight-280;
    },
    mounted: function () {
      var now = new Date();
      var year = now.getFullYear(); //得到年份
      var month = now.getMonth();//得到月份
      var month = month+1;
      var date = now.getDate();
      this.start = year+'-'+month+'-'+date,
      this.end = year+'-'+month+'-'+date
      this.getStock()
    },
    methods: {
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getStock(val, 1)
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getStock(this.PageSize, (val) * (this.pageSize))
      },
      getStock(a) {
        if(a == 1){
          this.currentPage = 1
        }
        var _this = this
        this.$http.post(''+'stock/admin/sellYingkui', {
          'k': localStorage['stock_token'],
          'user_name':this.user_name,
          'stock_code':this.stock_code,
          'page': this.currentPage,
          'start': this.start,
          'end': this.end,
          'limit': this.PageSize,
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              //_this.stockData = res.data.data.map(Number);
              var data = res.data.data.yk_list.data
              for (var i = 0; i<data.length; i++){
                data[i].yingkui = parseFloat(data[i].yingkui)
                data[i].trade_total_money = parseFloat(data[i].trade_total_money)
              }
              _this.todayAll = res.data.data.today_yingkui
              _this.monthAll = res.data.data.month_yingkui
              _this.stockData = res.data.data.yk_list.data;
              _this.total = res.data.data.yk_list.total
            }
          })
      },
    }
  }
</script>

<style scoped>
  .tong{
    margin-left: 30px;
    display: inline-block;
  }
</style>



