    <audio style="display: none" id="audio1" src="/1.mp3"></audio>
    <audio style="display: none" id="audio2" src="/2.mp3"></audio>
    <audio style="display: none" id="audio3" src="/3.mp3"></audio>
    <div v-show="NewWindow" :style="{left:left+'px',top:top+'px'}" class="contextmenu">
      <el-button size="small" @click="openNewWindow()">新窗口打开</el-button>
    </div>
