<script>
  export default {
    name: "index",
    data() {
      return {
        href:'',
        NewWindow: false,
        top: 0,
        left: 0,
        id:'',
        msgNum: 0,
        password: '',
        newpsw: '',
        newpsw2: '',
        MessageUser:'',
        MessageContent:'',
        MessageTime:'',
        newMessageShow: false,
        messageShow: false,
        messageEdit: false,
        passwordShow: false,
        messageList: [{}],
        activeIndex: "/index",
        menu:'',
        user_name:'',
        timer:'msg',
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        tableHeight:500,
        // 个数选择器（可修改）
        pageSizes: [100,200,300],
        // 默认每页显示的条数（可修改）
        PageSize: 100,
      }
    },
    mounted: function () {
      if (!localStorage['stock_token'] || !localStorage['menu'] ) {
        this.$router.push('/login')
      }
      this.menu = localStorage['menu']
      this.msgCount()
    },
    beforeDestroy() {
      clearInterval(this.timer);
      this.timer = '';
    },
    methods: {
      gettime(){
        return new Date().getTime()
      },
      openMenu(e,a) {
        this.href = a
        this.top = e.clientY // fix 位置bug
        this.left = e.clientX+30 // fix 位置bug
        this.NewWindow = !this.NewWindow
      },
      openNewWindow(){
        this.NewWindow = false;
        //益配资
        if(window.location.host == 'stock.ypzjyd.com'){
          window.open('http://'+window.location.host+'/ypz898Dau/#/'+this.href, '_blank')
        }else if (window.location.host == 'stock.ryjyd.com'){
          //荣耀配资
          window.open('http://'+window.location.host+'/rypz888Abc/#/'+this.href, '_blank')
        }else{
          window.open('http://'+window.location.host+'/admin/#/'+this.href, '_blank')
        }
      },
      exitLogin(){
        localStorage.clear();
        sessionStorage.clear();
        this.$router.push('/login')
      },
      menulist(a){
        //console.log(a)
        //console.log(this.menu.indexOf(a)!= -1)
        return this.menu.indexOf(a)!= -1
      },
      message() {
        this.getmessageList();
        this.messageShow = true
      },
      newMessage(){
        var _this = this
        this.$http.post('' + 'stock/admin/msgAdd', {
          'k': localStorage['stock_token'],
          'user_name': this.MessageUser,
          'msg': this.MessageContent,
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      msgCount() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'is_client': 0,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/msgCount', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              // _this.$message({
              //   message: res.data.msg,
              //   type: 'warning'
              // });
            } else {
              if (res.data.data.dd ==1){
                document.getElementById("audio1").play();
              }else if (res.data.data.dd ==2) {
                document.getElementById("audio2").play();
              }else if (res.data.data.dd ==3){
                document.getElementById("audio3").play();
              }
              if(res.data.data.dd != 0){
                _this.msgNum = res.data.data.count
              }
              clearInterval(_this.timer)
              _this.timer = setInterval(function () {
                _this.msgCount();
              },60*2*1000)
            }
          })
      },
      editMsg(data){
        this.messageEdit = true
        this.MessageTime = data.update_time
        this.MessageContent = data.title
        this.id = data.id
      },
      editMessage(){
        var _this = this
        this.$http.post('' + 'stock/admin/msgEdit', {
          'k': localStorage['stock_token'],
          'update_time':this.MessageTime,
          'id':this.id,
          'title':this.MessageContent
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.messageEdit = false
              _this.getmessageList()
            }
          })
      },
      delMsg(id) {
        var _this = this
        if (id != 0){
          this.id = id
        }
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post('' + 'stock/admin/msgDelete', {
            'k': localStorage['stock_token'],
            'ids': this.id,
          })
            .then(function (res) {
              if (res.data.code != 200) {
                _this.$message({
                  message: res.data.msg,
                  type: 'warning'
                });
              } else {
                _this.$message({
                  message: res.data.msg,
                  type: 'success'
                });
                _this.getmessageList()
              }
            })
        }).catch(() => {
        });
      },
      passwordEdit() {
        var _this = this
        if (this.newpsw != this.newpsw2) {
          _this.$message.error({
            message: '两次输入不一致',
          });
          return
        }
        var str = {
          'k': localStorage['stock_token'],
          'old': this.password,
          'new': this.newpsw,
          'new2': this.newpsw2,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/resetPas', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.passwordShow = false
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
            }
          })
      },
      getmessageList() {
        var _this = this
        this.$http.post('' + 'stock/admin/msgList', {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
          'user_name':this.user_name
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.messageList = res.data.data.data
              _this.totalCount = res.data.data.total
            }
          })
      },
      handleSelectionChange(val) {
        var s = ''
        for (var i = 0; i < val.length; i++) {
          s += val[i].id + ','
        }
        this.id = s.slice(0, -1);
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getmessageList()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getmessageList()
      },
    }
  }
</script>

