    width: 200px;
    left: 0;
    text-align: center;
    position: absolute;
  }
  .left-icon2 > i{
    color: #fff;
  }

  .left-icon > i{
    z-index: 9999;
    color: #fff;
  }

  .left-icon2 {
    color: #fff;
    line-height: 56px;
    position: absolute;
    left: 30px;
    z-index: 10;
  }

  .left-icon {
    color: #fff;
    z-index: 20;
    line-height: 56px;
    position: absolute;
    left: 20px;
  }

  .right-icon {
    color: #fff;
    line-height: 56px;
    position: absolute;
    right: 8px;
  }

  .footer {
    z-index: 10;
    width: 100%;
    bottom: 0;
    right: 0;
    position: fixed;
    height: 30px;
    background: #2c4a68;
  }

  .el-scrollbar__wrap::-webkit-scrollbar {
    display: none
  }

  .el-menu-item-group__title {
    padding: 0 !important;
  }

  .el-submenu .el-menu-item {
    text-align: right;
  }

  .el-scrollbar__wrap {
    overflow: hidden;
  }

  .logo {
    background-image: url("../../image/logo.jpg");
    width: 200px;
    height: 60px;
    float: left;
  }

  .el-menu-item {
    color: #303133;
  }

  .el-menu-demo {
    margin-left: 200px;
  }

  .el-scrollbar {
    background: #2c4a68;
    min-width: 200px;
  }

  .el-header, .el-footer {
    padding: 0;
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    overflow-x: hidden;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
  }

  .el-container {
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>



