<template>
  <div class="app">
    <el-card class="box-card" style="height: 110px;">
      <div style="height: 30px">
        <div style="float: left">
          <el-input placeholder="输入账户名搜索" v-model.trim="keyword" size="small" style="width: 150px"></el-input>
          <el-button type="primary" @click="getData(1)" size="small">搜索</el-button>
          <el-button @click="addShow = true" type="primary" size="small">入金/扩大</el-button>
          <el-button @click="inoutMore = true" type="success" size="small">亏损追补</el-button>
          <el-button @click="inoutOut = true;remark='赠送操盘金'" type="warning" size="small">赠送操盘金</el-button>
          <el-button @click="inoutMoney = true" type="danger" size="small">提盈</el-button>
          <el-button v-loading="loading" type="primary" @click="exportTable()" size="small">数据导出</el-button>
        </div>
        <div style="float: right">
          <span>重置用户名查询 &nbsp;</span>
          <el-input placeholder="输入用户名" v-model.trim="ResetName" size="small" style="width: 150px"></el-input>
          <el-button type="primary" @click="searchReset()" size="small">查询</el-button>
        </div>
        <div style="clear: both"></div>
      </div>
      <div style="margin-top: 15px">
        <el-date-picker
          v-model.trim="start"
          type="date"
          size="small"
          value-format="yyyy-MM-dd"
          placeholder="开始时间" width="170px" style="width: 160px">
        </el-date-picker>
        <el-date-picker
          v-model.trim="end"
          type="date"
          size="small"
          value-format="yyyy-MM-dd"
          placeholder="结束时间" width="170px" style="width: 160px">
        </el-date-picker>
        <el-button type="primary" @click="getData()" size="small">区间查询</el-button>
      </div>
      <div style="clear: both"></div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <div class="inoutAll">
        <div class="inoutInfo">今日入金: {{inoutData.jrrj}}</div>
        <div class="inoutInfo">今日配资: {{inoutData.jrpz}}</div>
        <div class="inoutInfo">今日提盈: {{inoutData.jrty}}</div>
        <div class="inoutInfo">今日追补: {{inoutData.jrzb}}</div>
      </div>
      <div style="height: 1px; background: #eee;margin: 10px 0"></div>
      <div class="inoutAll">
        <div class="inoutInfo">区间入金: {{inoutData.byrj}}</div>
        <div class="inoutInfo">区间配资: {{inoutData.bypz}}</div>
        <div class="inoutInfo">区间提盈: {{inoutData.byty}}</div>
        <div class="inoutInfo">区间追补: {{inoutData.byzb}}</div>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="loading">
      <el-table
        id="table-data"
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
      >
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="sn" label="流水号"></el-table-column>
        <el-table-column prop="user_name" label="账户名"></el-table-column>
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="tvalue" label="金额">
          <template slot-scope="scope">
            <span :style="scope.row.type == 0?'color:#f00':'color:#070'">
              {{scope.row.type == 0?'+'+scope.row.tvalue:'-'+scope.row.tvalue}}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="addtime" label="申请时间" width="170px"></el-table-column>
        <el-table-column prop="checktime" label="处理时间" width="170px"></el-table-column>
        <el-table-column label="处理状态">
          <template slot-scope="scope">
            {{scope.row.state == 1?'处理成功':'处理中'}}
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="备注"></el-table-column>
        <el-table-column prop="money_type_str" label="资金类型"></el-table-column>
        <el-table-column prop="description" label="编辑">
          <template slot-scope="scope">
            <el-button type="danger" @click="stockDelete(scope.row.id)" size="mini">撤销</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="入金/扩大" :visible.sync="addShow" width="400px">
      <div class="password-inp">
        <div class="password-name">用户账号</div>
        <el-input placeholder="请输入用户账号" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">金额</div>
        <el-input placeholder="请输入金额" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">备注</div>
        <el-input placeholder="请输入备注" v-model.trim="remark"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addShow = false">取 消</el-button>
        <el-button type="primary" @click="addStock(0)">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="追加资金" :visible.sync="inoutMore" width="400px">
      <div class="password-inp">
        <div class="password-name">用户账号</div>
        <el-input placeholder="请输入用户账号" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">金额</div>
        <el-input placeholder="请输入金额" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">备注</div>
        <el-input placeholder="请输入备注" v-model.trim="remark"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inoutMore = false">取 消</el-button>
        <el-button type="primary" @click="addMore" :loading="loading">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="赠送操盘金" :visible.sync="inoutOut" width="400px">
      <div class="password-inp">
        <div class="password-name">用户账号</div>
        <el-input placeholder="请输入用户账号" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">金额</div>
        <el-input placeholder="请输入金额" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">备注</div>
        <el-input placeholder="请输入备注" v-model.trim="remark" value="赠送操盘金"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inoutOut = false">取 消</el-button>
        <el-button type="primary" @click="inoutGive()" :loading="loading">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="提盈" :visible.sync="inoutMoney" width="400px">
      <div class="password-inp">
        <div class="password-name">用户账号</div>
        <el-input placeholder="请输入用户账号" v-model.trim="user_name"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">金额</div>
        <el-input placeholder="请输入金额" v-model.trim="money"></el-input>
      </div>
      <div class="password-inp">
        <div class="password-name">备注</div>
        <el-input placeholder="请输入备注" v-model.trim="remark"></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="inoutMoney = false">取 消</el-button>
        <el-button type="primary" @click="addOut" :loading="loading">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="重置记录" :visible.sync="reset" width="600px">
      <el-table
        :data="resetList"
        style="width: 100%"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
      >
        <el-table-column prop="user_name" label="账户名"></el-table-column>
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="create_time" label="重置日期"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
  import fileSaver from 'file-saver'
  import xlsx from 'xlsx'
  export default {
    data() {
      return {
        reset:false,
        loading: false,
        id: '',
        start: '',
        end: '',
        keyword: '',
        ResetName:'',
        addShow: false,
        inoutMore: false,
        inoutOut: false,
        inoutMoney: false,
        user_name: '',
        resetList:'',
        money: '',
        remark: '',
        type: '0',
        options: [{
          value: '0',
          label: '客户入金'
        }],
        moneyoptions: [{
          value: '0',
          label: '客户资金'
        }, {
          value: '1',
          label: '系统垫资'
        }],
        // 总数据
        tableHeight: 500,
        tableData: [],
        inoutData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        // 个数选择器（可修改）
        pageSizes: [20, 50, 150, 300],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
      }
    },
    watch: {
      $route() {
        this.tableHeight = window.innerHeight - 395;
        this.getData(this.PageSize, this.currentPage)
      }
    },
    created: function () {
      this.tableHeight = window.innerHeight - 395;
      this.getData(this.PageSize, this.currentPage)
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData(a) {
        if(a == 1){
          this.currentPage = 1
        }
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutList', {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'start': this.start,
          'end': this.end,
          'limit': this.PageSize,
          'keyword': this.keyword
        })
          .then(function (res) {
            _this.loading = false
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.inoutData = res.data.data;
              _this.tableData = res.data.data.list.data;
              _this.totalCount = res.data.data.list.total
            }
          })
      },
      searchReset(){
        this.reset = true
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'user_name': this.ResetName,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/searchReset', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            _this.resetList = res.data.data
          })
      },
      stockDelete(id) {
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteStock(id)
        }).catch(() => {
        });
      },
      deleteStock(id) {
        if (id != 0) {
          this.id = id
        }
        var _this = this
        this.$http.post('' + 'stock/admin/inoutCancel', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      //入金扩大
      addStock(a) {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutAdd', {
          'k': localStorage['stock_token'],
          'type': a,
          'moneytype': this.moneytype,
          'user_name': this.user_name,
          'multiple':this.moneytype,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            _this.user_name = ''
            _this.money = ''
            _this.remark = ''
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      inoutGive() {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutGive', {
          'k': localStorage['stock_token'],
          'user_name': this.user_name,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            _this.user_name = ''
            _this.money = ''
            _this.remark = ''
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      //追加资金
      addMore() {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutMore', {
          'k': localStorage['stock_token'],
          'user_name': this.user_name,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      //提盈
      addOut() {
        this.loading = true
        var _this = this
        this.$http.post('' + 'stock/admin/inoutOut', {
          'k': localStorage['stock_token'],
          'user_name': this.user_name,
          'money': this.money,
          'remark': this.remark,
        })
          .then(function (res) {
            _this.loading = false
            _this.user_name = ''
            _this.money = ''
            _this.remark = ''
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
      exportData(){
        this.loading = true
        var _this = this
        if(_this.totalCount > 5000){
          _this.$message({
            message: '导出数据最大数量为5000,请减少数据',
            type: 'warning'
          });
          _this.loading = false
          return
        }
        this.$http.post('' + 'stock/admin/inoutList', {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'start': this.start,
          'end': this.end,
          'limit': 5000,
          'keyword': this.keyword
        })
          .then(function (res) {
            _this.loading = false
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.inoutData = res.data.data;
              _this.tableData = res.data.data.list.data;
              _this.totalCount = res.data.data.list.total
              if(_this.totalCount > 5000){
                _this.$message({
                  message: '导出数据最大数量为5000,请减少数据',
                  type: 'warning'
                });
                _this.loading = false
                return
              }
              setTimeout(function () {
                _this.exportTable()
              }, 3000)
            }
          })
      },
      //定义导出Excel表格事件
      exportTable() {
        /* 从表生成工作簿对象 */
        var wb = xlsx.utils.table_to_book(document.querySelector("#table-data"));
        /* 获取二进制字符串作为输出 */
        var wbout = xlsx.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array"
        });
        var now = new Date();
        var year = now.getFullYear(); //得到年份
        var month = now.getMonth();//得到月份
        month = month + 1;
        var date = now.getDate();
        var xlname = year + '' + month + '' + date
        var name = window.location.host + xlname
        try {
          fileSaver.saveAs(
            new Blob([wbout], {type: "application/octet-stream"}),
            //设置导出文件名称
            name + ".xlsx"
          );
          this.loading = false
        } catch (e) {
          if (typeof console !== "undefined") {
            alert('导出数据出错')
          }
        }
        return wbout;
      }
    },
  }
</script>

<style scoped>
  .password-inp {
    margin: 10px auto;
    max-width: 220px;
  }

  .inoutAll {
    display: flex;
  }

  .inoutInfo {
    flex: 1;
  }

  .password-name {
    font-size: 15px;
    padding: 5px;
  }
</style>



