<template>
  <div>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col class="card" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div class="lable">选择图片(建议分辨率360*180)</div>
          <el-upload
            class="upload-demo"
            ref="upload"
            :multiple = false
            :on-success="handleAvatarSuccess"
            :on-error="handleAvatarErr"
            :action= httpUpload
            :file-list="fileList"
            :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
          </el-upload>
        </el-col>
      </el-row>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <div class="text item">
        <el-table
          :data="tableData"
          border
          tooltip-effect="dark"
          style="width: 100%" stripe           :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"           :header-cell-style="{'text-align':'center'}">
          <el-table-column sortable prop="url" label="图片">
            <template slot-scope="scope">
              <el-image style="width: 180px; height: 90px" :src="scope.row.url" ></el-image>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="上传时间" width="170px"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="danger" @click="imgDel(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: "add",
    data() {
      return {
        data:'',
        imageUrl: '',
        httpUpload: '',
        tableData:[{}],
        fileList:[]
      }
    },
    mounted: function () {
      this.httpUpload = 'http://'+window.location.host+'/stock/upload/uploadImg'
      this.getLists();
    },
    watch:{
      $route(){
        this.getLists()
      }
    },
    methods: {
      getLists() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/carousels', {
          'data': this.data
        })
        .then(function (res) {
         _this.$VerificationLogin(res)
          if (res.data.code != 200) {
            _this.$message({
              message: res.data.msg,
              type: 'warning'
            });
          } else {
            _this.tableData = res.data.data.data;
          }
        })
      },
      submitUpload() {
        this.$refs.upload.submit();
      },
      imgDel(id){
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.imgDelete(id)
        }).catch(() => {
        });
      },
      imgDelete(id) {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id':id
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/carouselsDelete', {
          'data': this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getLists()
            }
          })
      },
      handleAvatarSuccess(){
        this.$message({
          message: '上传成功',
          type: 'success'
        });
        this.getLists()
      },
      handleAvatarErr(){
        this.$message.error({
          message: '上传失败',
        });
      }
    },
  }
</script>

<style scoped>
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .lable {
    font-size: 14px;
    padding: 5px 0;
    text-align: left;
  }
  .card {
    margin-bottom: 20px;
  }
</style>



