<template>
  <div>
    <el-card class="box-card">
      <div class="button-list">
        <el-row>
          <el-input
            placeholder="输入账户名搜索"
            v-model.trim="user_name"
            size="small"
            style="width: 150px"
          ></el-input>
          <el-input
            placeholder="输入股票代码搜索"
            v-model.trim="stock_code"
            size="small"
            style="width: 150px"
          ></el-input>
          <el-date-picker
            v-model.trim="start"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            width="170px"
            style="width: 160px"
          ></el-date-picker>
          <el-date-picker
            v-model.trim="end"
            type="date"
            size="small"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            width="170px"
            style="width: 160px"
          ></el-date-picker>
          <el-button @click="getStock(1)" type="primary" size="small">查询</el-button>
        </el-row>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card" v-loading="Loading">
      <div class="text item">
        <el-table
          :data="stockData"
          border
          tooltip-effect="dark"
          style="width: 100%"
          :height="tableHeight"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}"
        >
          <el-table-column prop="user_name" label="用户账号"></el-table-column>
          <el-table-column prop="name" label="用户名"></el-table-column>
          <el-table-column sortable prop="addtime" label="委托时间" width="170px"></el-table-column>
          <el-table-column sortable prop="stock_code" label="证券代码"></el-table-column>
          <el-table-column prop="stock_name" label="证券名称"></el-table-column>
          <el-table-column sortable prop="weituo_way" label="操作">
            <template slot-scope="scope">
              <span v-if="scope.row.weituo_way == '卖出'" style="color: #f00">卖出</span>
              <span v-else>买入</span>
            </template>
          </el-table-column>
          <el-table-column sortable prop="stock_price" label="委托价格">
            <template
              slot-scope="scope"
            >{{scope.row.is_market_price == 1?'市价':parseFloat(scope.row.stock_price).toFixed(2)}}</template>
          </el-table-column>
          <el-table-column sortable prop="real_price" label="成交价"></el-table-column>
          <el-table-column sortable prop="stock_num" label="委托数量"></el-table-column>
          <el-table-column sortable prop="trade_num" label="成交数量"></el-table-column>
          <el-table-column sortable prop="total_money" label="成交金额"></el-table-column>
          <el-table-column sortable prop="update_time" label="成交时间" width="170px">
            <template slot-scope="scope">
              <span v-if="scope.row.state == 1">{{scope.row.update_time}}</span>
            </template>
          </el-table-column>
          <el-table-column sortable prop="cancel_success_time" label="撤单时间" width="170px"></el-table-column>
          <el-table-column sortable prop="state_text" label="状态">
            <template slot-scope="scope">
              <span v-if="scope.row.is_pg == 1">配股到账</span>
              <span v-if="scope.row.is_pg != 1">{{scope.row.state_text}}</span>
            </template>
          </el-table-column>
          <el-table-column sortable prop="state_text" label="是否强平">
            <template slot-scope="scope">
              <span v-if="scope.row.is_admin == 1">已强平</span>
              <span v-if="scope.row.is_admin == 2">管理员平仓</span>
            </template>
          </el-table-column>
          <el-table-column sortable prop="stock_price" label="操作">
            <template slot-scope="scope">
              <el-button
                v-if="scope.row.state == 0"
                size="mini"
                type="danger"
                @click="stockDelete(scope.row.id)"
              >撤销</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="height: 15px"></div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  name: "index",
  data() {
    return {
      Loading: true,
      user_name: "",
      total: 1,
      PageSize: 20,
      currentPage: 1,
      data: "",
      start: "",
      end: "",
      stock_code: "",
      tableHeight: 500,
      stockData: [{}],
      pageSizes: [20, 50, 150, 300],
      value: "",
      timer: "",
      timerType: true
    };
  },
  watch: {
    $route() {
      this.tableHeight = window.innerHeight - 280;
      this.getStock();
      clearInterval(this.timer);
      this.setTimedata();
    }
  },
  created: function() {
    this.tableHeight = window.innerHeight - 280;
  },
  mounted: function() {
    var now = new Date();
    var year = now.getFullYear(); //得到年份
    var month = now.getMonth(); //得到月份
    var month = month + 1;
    var date = now.getDate();
    (this.start = year + "-" + month + "-" + date),
      (this.end = year + "-" + month + "-" + date);
    this.getStock();
    clearInterval(this.timer);
    this.setTimedata();
  },
  methods: {
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 点击每页显示的条数时，显示第一页
      this.getStock();
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      // 切换页码时，要获取每页显示的条数
      this.getStock();
    },
    setTimedata() {
      var _this = this;
      // 定时获取最新快讯
      _this.timer = setInterval(() => {
        _this.getStock();
      }, 10000);
      //页面销毁时清除定时获取
      this.$once("hook:beforeDestroy", () => {
        clearInterval(_this.timer);
      });
    },
    getStock(a) {
      if(a == 1){
        this.currentPage = 1
      }
      if (this.timerType) {
        this.timerType = false;
        var _this = this;
        this.$http
          .post("" + "stock/admin/weituoList", {
            k: localStorage["stock_token"],
            start: this.start,
            end: this.end,
            user_name: this.user_name,
            stock_code: this.stock_code,
            page: this.currentPage,
            limit: this.PageSize
          })
          .then(function(res) {
            _this.$VerificationLogin(res);
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              _this.Loading = false;
              //_this.stockData = res.data.data.map(Number);
              var data = res.data.data.data;
              for (var i = 0; i < data.length; i++) {
                data[i].real_price = parseFloat(data[i].real_price);
                data[i].total_money = parseFloat(data[i].total_money);
              }
              _this.stockData = data;
              _this.total = res.data.data.total;
              _this.timerType = true;
            }
          });
      } else {
      }
    },
    stockDelete(id) {
      this.$confirm("此操作将无法恢复, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteStock(id);
        })
        .catch(() => {});
    },
    deleteStock(id) {
      if (id != 0) {
        this.id = id;
      }
      var _this = this;
      this.$http
        .post("" + "stock/admin/cancelWeituoAdmin", {
          k: localStorage["stock_token"],
          id: this.id
        })
        .then(function(res) {
          if (res.data.code != 200) {
            _this.$message({
              message: res.data.msg,
              type: "warning"
            });
          } else {
            _this.$message({
              message: res.data.msg,
              type: "success"
            });
            _this.getStock();
          }
        });
    }
  }
};
</script>

<style scoped>
</style>



