<template>
  <div class="app">
    <el-card class="box-card">
      <el-table
        :data="tableData"
        style="width: 100%"
        stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="add_time" label="时间" width="170px"></el-table-column>
        <el-table-column prop="admin_name" label="用户名"></el-table-column>
        <el-table-column prop="description" label="操作"></el-table-column>
        <el-table-column prop="ip" label="IP"></el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-button type="small" style="float: right" @click="deleteLog()">删除</el-button>
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        id: '',
        // 总数据
        tableData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        // 个数选择器（可修改）
        pageSizes: [20,50,150,300],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
      }
    },
    watch:{
      $route(){
        this.getData(this.PageSize, this.currentPage)
      }
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/admin/logs', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      deleteLog() {
        var _this = this
        this.$http.post( '' + 'stock/admin/logsDelete', {
          'k': localStorage['stock_token'],
          'id': this.id
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        var s = ''
        for (var i = 0; i<val.length; i++){
          s += val[i].log_id+','
        }
        this.id = s.slice(0,-1);
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
    },
    created: function () {
      this.getData(this.PageSize, this.currentPage)
    }
  }
</script>



