            <template slot-scope="scope">
              <span v-if="scope.row.is_pg == 1">配股到账</span>
              <span v-if="scope.row.is_pg != 1">{{scope.row.state_text}}</span>
            </template>
          </el-table-column>
          <el-table-column sortable prop="stock_price" label="强平">
            <template slot-scope="scope">
              <span v-if="scope.row.is_admin == 1">已强平</span>
              <span v-if="scope.row.is_admin == 2">管理员平仓</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="entrustListsShow = false">关 闭</el-button>
      </div>
    </el-dialog>
    <el-dialog title="股票交割" :visible.sync="deliveryOrderShow" width="85%">
      <div v-loading="modoLoading">
        <el-table
          :data="deliveryOrderData"
          style="width: 100%"
          stripe
          :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
          :header-cell-style="{'text-align':'center'}"
          border
        >
          <el-table-column prop="user_name" label="用户账号"></el-table-column>
          <el-table-column prop="update_time" label="成交时间" width="170px"></el-table-column>
          <el-table-column prop="stock_code" label="证券代码"></el-table-column>
          <el-table-column prop="stock_name" label="证券名称"></el-table-column>
          <el-table-column prop="date" label="动作">
            <template slot-scope="scope">
              <span v-if="scope.row.type==1">买入</span>
              <span v-else style="color: #f00">卖出</span>
            </template>
          </el-table-column>
          <el-table-column prop="trade_num" label="成交数量"></el-table-column>
          <el-table-column prop="trade_price" label="成交价格"></el-table-column>
          <el-table-column label="成交金额">
            <template
              slot-scope="scope"
            >{{parseFloat(scope.row.trade_price * scope.row.trade_num).toFixed(2)}}
            </template>
          </el-table-column>
          <el-table-column prop="sx" label="手续费"></el-table-column>
          <el-table-column prop="yk" label="印花税"></el-table-column>
          <el-table-column prop="ghf" label="过户费"></el-table-column>
          <el-table-column prop="yingkui" label="盈亏">
            <template slot-scope="scope">
              <span v-if="scope.row.yingkui<0" style="color: #070">{{scope.row.yingkui}}</span>
              <span v-if="scope.row.yingkui==0">{{scope.row.yingkui}}</span>
              <span v-if="scope.row.yingkui>0" style="color: #f00">{{scope.row.yingkui}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="deliveryOrderShow = false">关 闭</el-button>
      </div>
    </el-dialog>
    <el-dialog title="已删用户恢复" :visible.sync="removeUserShow" width="400px">
      <div class="password-inp">
        <div style="padding: 5px 0">账号</div>
        <el-input
          placeholder="请输入账号"
          style="width: 200px"
          size="small"
          v-model.trim="user_name"
          clearable
        ></el-input>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="usersRecovery()">恢复</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        canEditName:false,
        clickLoading: false,
        removeUserShow: false,
        multiple: "",
        Loading: true,
        id: "",
        removeUser: [],
        stockEditShow: false,
        entrustListsShow: false,
        deliveryOrderShow: false,
        modoLoading: true,
        stockHoldShow: false,
        newUserShow: false,
        tableHeight: 500,
        total: 1,
        PageSize: 20,
        currentPage: 1,
        data: "",
        state: "",
        user_name: "",
        name: "",
        warn_money: "",
        initial_money: "",
        user_money: "",
        remark: "",
        forvalue: "",
        pc_money: "",
        password: "",
        end_time: "",
        mobile: "",
        sx_qian: "",
        sx_min: "",
        yinhua_qian: "",
        ghf_qian: "",
        ghf_min: "",
        open: "0",
        cyb_switch_user: "1",
        tableData: [{}],
        stockHoldData: [{}],
        entrustListsData: [{}],
        deliveryOrderData: [{}],
        totalCount: 1,
        pageSizes: [20, 50, 150, 300],
        timer: null,
        timerType: true,
        selectCheck: []
      };
    },
    watch: {
      $route() {
        if(window.location.host == 'stock.gypz666.com'){
          this.canEditName = true
        }
        this.tableHeight = window.innerHeight - 280;
        this.getData();
        clearInterval(this.timer);
        this.setTimedata();
      }
    },
    created: function () {
      if(window.location.host == 'stock.gypz666.com'){
        this.canEditName = true
      }
      this.tableHeight = window.innerHeight - 280;
      this.getData()
      clearInterval(this.timer);
      this.setTimedata();
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      newUserS() {
        this.newUserShow = true;
        var _this = this;
        this.$http
          .post("" + "stock/admin/configs", {
            k: localStorage["stock_token"]
          })
          .then(function (res) {
            _this.password = res.data.data.default_pas;
            _this.sx_qian = res.data.data.sx_qian;
            _this.sx_min = res.data.data.sx_min;
            _this.yinhua_qian = res.data.data.yinhua_qian;
            _this.ghf_qian = res.data.data.ghf_qian;
            _this.ghf_min = res.data.data.ghf_min;
          });
      },
      setTimedata() {
        var _this = this;
        // 定时获取最新快讯
        _this.timer = setInterval(() => {
          this.getData();
        }, 3000);
        //页面销毁时清除定时获取
        this.$once("hook:beforeDestroy", () => {
          clearInterval(_this.timer);
        });
      },
      getData(a) {
        if (a == 1) {
          this.clickLoading = true
          this.currentPage = 1
        }
        if (this.timerType) {
          this.timerType = false;
          var _this = this;
          var str = {
            k: localStorage["stock_token"],
            page: this.currentPage,
            limit: this.PageSize,
            user_name: this.user_name
          };
          this.data = this.KEY(str);
          this.$http
            .post("" + "stock/admin/users", {
              data: this.data
            })
            .then(function (res) {
              _this.$VerificationLogin(res);
              if (res.data.code != 200) {
                _this.$message({
                  message: res.data.msg,
                  type: "warning"
                });
              } else {
                _this.clickLoading = false;
                _this.Loading = false;
                var data = res.data.data.list.data;
                for (var i = 0; i < data.length; i++) {
                  data[i].user_money = parseFloat(data[i].user_money);
                  data[i].frozen_money = parseFloat(data[i].frozen_money);
                  data[i].can_get_money = parseFloat(data[i].can_get_money);
                  data[i].yk_bl = parseFloat(data[i].yk_bl);
                  data[i].initial_money = parseFloat(data[i].initial_money);
                  data[i].market = parseFloat(data[i].market);
                  data[i].total_money = parseFloat(data[i].total_money);
                }
                _this.tableData = data;
                _this.totalCount = res.data.data.list.total;
                _this.timerType = true;
              }
            }).catch(error=>{
            _this.$message({
              message: '请求超时,请刷新后重试',
              type: "error"
            });
          });
        } else {
        }
      },
      usersAdd() {
        var _this = this;
        if (
          this.multiple == "" ||
          this.user_name == "" ||
          this.end_time == "" ||
          this.mobile == ""
        ) {
          _this.$message({
            message: "必填项,不能为空!",
            type: "warning"
          });
          return;
        }
        this.multiple = parseFloat(_this.multiple);
        if (this.multiple <= 0) {
          _this.$message({
            message: "杠杆必须大于0",
            type: "warning"
          });
          return;
        }
        this.$http
          .post("" + "stock/admin/usersAdd", {
            k: localStorage["stock_token"],
            user_name: this.user_name,
            name: this.name,
            multiple: this.multiple,
            password: this.password,
            end_time: this.end_time,
            mobile: this.mobile,
            remark: this.remark,
            sx_qian: this.sx_qian,
            sx_min: this.sx_min,
            yinhua_qian: this.yinhua_qian,
            ghf_qian: this.ghf_qian,
            ghf_min: this.ghf_min
          })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: "success"
              });
              _this.newUserShow = false;
              _this.user_name = "";
              _this.password = "";
              _this.end_time = "";
              _this.name = "";
              _this.mobile = "";
              _this.warn_money = "";
              _this.pc_money = "";
              _this.remark = "";
              _this.multiple = "";
              _this.getData();
            }
          });
      },
      stockEdit(user) {
        this.stockEditShow = true;
        this.id = user.user_id;
        this.multiple = user.multiple;
        this.password = user.password;
        this.name = user.name;
        this.state = user.state;
        this.end_time = user.end_time;
        this.mobile = user.mobile;
        this.open = user.open + "";
        this.cyb_switch_user = user.cyb_switch_user + "";
        this.warn_money = user.warn_money;
        this.pc_money = user.pc_money;
        this.initial_money = user.initial_money;
        this.forvalue = user.forvalue;
        this.remark = user.remark;
        this.user_money = user.user_money;
        this.sx_qian = user.sx_qian;
        this.sx_min = user.sx_min;
        this.yinhua_qian = user.yinhua_qian;
        this.ghf_qian = user.ghf_qian;
        this.ghf_min = user.ghf_min;
      },
      userEdit() {
        var _this = this;
        if (this.multiple == "" || this.end_time == "" || this.mobile == "") {
          _this.$message({
            message: "必填项,不能为空!",
            type: "warning"
          });
          return;
        }
        this.multiple = parseFloat(_this.multiple);
        if (this.multiple <= 0) {
          _this.$message({
            message: "杠杆必须大于0",
            type: "warning"
          });
          return;
        }
        this.$http
          .post("" + "stock/admin/usersEdit", {
            k: localStorage["stock_token"],
            id: this.id,
            user_name: this.user_name,
            name: this.name,
            multiple: this.multiple,
            password: this.password,
            end_time: this.end_time,
            mobile: this.mobile,
            open: this.open,
            cyb_switch_user: this.cyb_switch_user,
            state: this.state,
            initial_money: this.initial_money,
            forvalue: this.forvalue,
            warning: this.warn_money,
            pcvalue: this.pc_money,
            user_money: this.user_money,
            remark: this.remark,
            sx_qian: this.sx_qian,
            sx_min: this.sx_min,
            yinhua_qian: this.yinhua_qian,
            ghf_qian: this.ghf_qian,
            ghf_min: this.ghf_min
          })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: "success"
              });
              _this.getData();
              _this.stockEditShow = false;
              _this.user_name = "";
              _this.password = "";
              _this.end_time = "";
              _this.name = "";
              _this.mobile = "";
              _this.warn_money = "";
              _this.pc_money = "";
              _this.remark = "";
              _this.multiple = "";
            }
          });
      },
      usersRecovery() {
        var _this = this;
        this.$confirm("确定要恢复用户" + _this.user_name + "吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$http
              .post("" + "stock/admin/usersRecovery", {
                k: localStorage["stock_token"],
                user_name: _this.user_name
              })
              .then(function (res) {
                if (res.data.code != 200) {
                  _this.$message({
                    message: res.data.msg,
                    type: "warning"
                  });
                } else {
                  _this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  _this.user_name = "";
                  _this.removeUserShow = false;
                  _this.getData();
                }
              });
          })
          .catch(() => {
          });
      },
      usersDelete(id) {
        var _this = this;
        this.$confirm("此操作将无法恢复, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            if (id != 0) {
              _this.id = id;
            }
            this.$http
              .post("" + "stock/admin/usersDelete", {
                k: localStorage["stock_token"],
                id: _this.id
              })
              .then(function (res) {
                if (res.data.code != 200) {
                  _this.$message({
                    message: res.data.msg,
                    type: "warning"
                  });
                } else {
                  _this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  _this.getData();
                }
              });
          })
          .catch(() => {
          });
      },
      usersReset(id) {
        var _this = this;
        this.$confirm("此操作将无法恢复, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$http
              .post("" + "stock/admin/usersReset", {
                k: localStorage["stock_token"],
                id: id
              })
              .then(function (res) {
                if (res.data.code != 200) {
                  _this.$message({
                    message: res.data.msg,
                    type: "warning"
                  });
                } else {
                  _this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  _this.getData();
                }
              });
          })
          .catch(() => {
          });
      },
      usersUpdate(id) {
        var _this = this;
        this.$http
          .post("" + "stock/admin/usersUpdate", {
            k: localStorage["stock_token"],
            id: id
          })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: "success"
              });
              _this.getData();
            }
          });
      },
      stockHold(user_name) {
        this.stockHoldShow = true;
        var _this = this;
        var str = {
          k: localStorage["stock_token"],
          id: user_name
        };
        this.data = this.KEY(str);
        this.$http
          .post("" + "stock/admin/lcList", {
            data: this.data
          })
          .then(function (res) {
            _this.modoLoading = false;
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              _this.stockHoldData = res.data.data.data.data;
            }
          });
      },
      entrustLists(user_name) {
        this.entrustListsShow = true;
        var _this = this;
        var str = {
          k: localStorage["stock_token"],
          id: user_name
        };
        this.data = this.KEY(str);
        this.$http
          .post("" + "stock/admin/weituoList", {
            data: this.data
          })
          .then(function (res) {
            _this.modoLoading = false;
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              var data = res.data.data.data;
              for (var i = 0; i < data.length; i++) {
                data[i].real_price = parseFloat(data[i].real_price);
                data[i].total_money = parseFloat(data[i].total_money);
              }
              _this.entrustListsData = data;
            }
          });
      },
      deliveryOrder(user_name) {
        this.deliveryOrderShow = true;
        var _this = this;
        var str = {
          k: localStorage["stock_token"],
          id: user_name
        };
        this.data = this.KEY(str);
        this.$http
          .post("" + "stock/admin/jgList", {
            data: this.data
          })
          .then(function (res) {
            _this.modoLoading = false;
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              var data = res.data.data.list.data;
              _this.deliveryOrderData = data;
            }
          });
      },
      PCstock(id) {
        this.$confirm("此操作将无法恢复, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            if (id != 0) {
              this.id = id;
            }
            this.stockPingCang();
          })
          .catch(() => {
          });
      },
      stockPingCang() {
        var _this = this;
        var str = {
          k: localStorage["stock_token"],
          id: this.id
        };
        this.data = this.KEY(str);
        this.$http
          .post("" + "stock/admin/lcPcById", {
            data: this.data
          })
          .then(function (res) {
            _this.Loading = false;
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: "warning"
              });
            } else {
              _this.id = "";
              _this.$message({
                message: res.data.msg,
                type: "success"
              });
            }
          });
      },
      handleSelectionChange(val) {
        if (val.length != 0) {
          clearTimeout(this.timer);
        } else {
          this.getData();
        }
        var s = "";
        for (var i = 0; i < val.length; i++) {
          s += val[i].user_id + ",";
        }
        this.id = s.slice(0, -1);
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val;
        // 点击每页显示的条数时，显示第一页
        this.getData();
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1;
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val;
        // 切换页码时，要获取每页显示的条数
        this.getData(this.PageSize, val * this.pageSize);
      }
    }
  };
</script>

<style scoped>
  .el-button + .el-button {
    margin-left: 0;
  }

  .mini-btn {
    margin: 2px !important;
    padding: 3px 5px;
  }

  .password-inp {
    margin: 10px auto;
    max-width: 220px;
  }

  .password-name {
    font-size: 15px;
    padding: 5px;
  }
</style>



