<template>
  <div>
    <el-card class="box-card">
      <div class="">
        <el-button @click="newShow = true" size="small">新增用户</el-button>
        <el-input placeholder="输入需要查询的角色名" style="width: 150px" size="small" v-model.trim="keyword"></el-input>
        <el-button type="primary" size="small" @click="searchUser(1)">搜索</el-button>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-card class="box-card">
      <el-table
        :data="tableData"
        style="width: 100%" stripe
        :cell-style="{'text-align': 'center','padding-top':'5px','padding-bottom':'5px'}"
        :header-cell-style="{'text-align':'center'}"
        border>
        <el-table-column prop="id" label="id"></el-table-column>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="title" label="角色名称"></el-table-column>
        <el-table-column prop="loginnum" label="登录次数"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            {{scope.row.status == 1?'开启':'关闭'}}
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="添加时间" width="170px"></el-table-column>
        <el-table-column prop="update_time" label="更新时间" width="170px"></el-table-column>
        <el-table-column prop="description" label="操作" width="170px">
          <template slot-scope="scope">
            <el-button style="margin: 0 5px" type="primary" @click="editShowUser(scope.row)" size="mini">编辑</el-button>
            <el-button @click="deleteUser(scope.row.id)" style="margin: 0 5px" type="danger" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="height: 10px"></div>
      <div class="tabListPage">
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="pageSizes"
                       :page-size="PageSize" layout="total, sizes, prev, pager, next, jumper"
                       :total="totalCount">
        </el-pagination>
      </div>
    </el-card>
    <div style="height: 15px"></div>
    <el-dialog title="修改用户" :visible.sync="editShow" width="300px">
      <div class="password-inp" style="width: 217px">
        <el-input placeholder="请输入用户名" v-model.trim="username"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">角色所属分类：</div>
        <el-select v-model.trim="gg" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.title"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0">设置密码：</div>
        <el-input placeholder="请输入密码" v-model.trim="editpassword" type="password"></el-input>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0">真实姓名：</div>
        <el-input placeholder="真实姓名" v-model.trim="real_name"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">状态</div>
        <el-radio v-model.trim="radio" label="1">开启</el-radio>
        <el-radio v-model.trim="radio" label="0">关闭</el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editShow = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="新增用户" :visible.sync="newShow" width="300px">
      <div class="password-inp" style="width: 217px">
        <el-input placeholder="请输入用户名" v-model.trim="title"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">角色所属分类：</div>
        <el-select v-model.trim="id" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.title"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0">设置密码：</div>
        <el-input placeholder="请输入密码" v-model.trim="password" type="password"></el-input>
      </div>
      <div class="password-inp" style="width: 217px">
        <div style="margin: 10px 0">真实姓名：</div>
        <el-input placeholder="真实姓名" v-model.trim="real_name"></el-input>
      </div>
      <div class="password-inp">
        <div style="margin: 10px 0">状态</div>
        <el-radio v-model.trim="radio" label="1">开启</el-radio>
        <el-radio v-model.trim="radio" label="0">关闭</el-radio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="newShow = false">取 消</el-button>
        <el-button type="primary" @click="newUser">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'index',
    data() {
      return {
        id:'',
        gg:'',
        radio:'1',
        value:'',
        password:'',
        real_name:'',
        username:'',
        editpassword:'',
        title:'',
        keyword:'',
        options:[{}],
        editShow:false,
        newShow:false,
        // 总数据
        tableData: [],
        // 默认显示第几页
        currentPage: 1,
        // 总条数，根据接口获取数据长度(注意：这里不能为空)
        totalCount: 1,
        // 个数选择器（可修改）
        pageSizes: [20,50,150,300],
        // 默认每页显示的条数（可修改）
        PageSize: 20,
      }
    },
    watch:{
      $route(){
        this.getGroups()
        this.getData(this.PageSize, this.currentPage)
      }
    },
    methods: {
      // 将页码，及每页显示的条数以参数传递提交给后台
      getData() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'page': this.currentPage,
          'limit': this.PageSize,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/users', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      getGroups() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/groupsAll', {
          data: this.data
        })
          .then(function (res) {
            _this.$VerificationLogin(res)
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.options = res.data.data
            }
          })
      },
      deleteUser(id){
        this.$confirm('此操作将无法恢复, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteLog(id)
        }).catch(() => {
        });
      },
      editShowUser(data){
        this.editShow = true,
        this.id = data.id,
        this.real_name = data.real_name
        this.username = data.username
      },
      editUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': this.id,
          'u': this.username,
          'p': this.editpassword,
          'r': this.real_name,
          'g': this.gg,
          's': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/usersEdit', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      newUser() {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'u': this.title,
          'p': this.password,
          'r': this.real_name,
          'g': this.id,
          's': this.radio,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/usersAdd', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      searchUser(){
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'keyword': this.keyword,
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/users', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.tableData = res.data.data.data;
              _this.totalCount = res.data.data.total
            }
          })
      },
      deleteLog(id) {
        var _this = this
        var str = {
          'k': localStorage['stock_token'],
          'id': id
        };
        this.data = this.KEY(str);
        this.$http.post('' + 'stock/sys/usersDelete', {
          data: this.data
        })
          .then(function (res) {
            if (res.data.code != 200) {
              _this.$message({
                message: res.data.msg,
                type: 'warning'
              });
            } else {
              _this.$message({
                message: res.data.msg,
                type: 'success'
              });
              _this.getData()
            }
          })
      },
      // 每页显示的条数
      handleSizeChange(val) {
        // 改变每页显示的条数
        this.PageSize = val
        // 点击每页显示的条数时，显示第一页
        this.getData()
        // 注意：在改变每页显示的条数时，要将页码显示到第一页
        this.currentPage = 1
      },
      // 显示第几页
      handleCurrentChange(val) {
        // 改变默认的页数
        this.currentPage = val
        // 切换页码时，要获取每页显示的条数
        this.getData()
      },
    },
    created: function () {
      this.getGroups()
      this.getData(this.PageSize, this.currentPage)
    }
  }
</script>

<style scoped>
</style>



